//*   custom color patlette
$int-orange:                           hsla(6%, 100%, 36%, 1);
$azureish-white:                       hsla(207%, 64%, 91%, 1);
$dark-sea-green:                       hsla(143%, 30%, 60%, 1);
$babe-blue-eyes:                       hsla(203%, 75%, 78%, 1);
$gunmetal:                             hsla(180%, 13%, 20%, 1);


$azureish-white:                       #daeaf7;
$dark-sea-green:                       #79b791;
$babe-blue-eyes:                       #9dd1f1;


$spiro-disco-ball:                     #23C9FF;
$magic-mint:                           #A9FFCB;


$ash-gray:                             #B3BFBF;
$gunmetal-gray:                        #708787;
$gunmetal:                             rgba(45, 58, 58, 1);
$rich-primary:                           darken(#daeaf7, 40%);
$deep-primary:                           darken(#daeaf7, 80%);

$int-orange:                           #ba1200;


// $aero-blue: #caffd0;




//colors
$white:                                #fff !default;
$gray-100:                             #f8f9fa !default;
$gray-200:                             #e9ecef !default;
$gray-300:                             #dee2e6 !default;
$gray-400:                             #ced4da !default;
$gray-500:                             #adb5bd !default;
$gray-600:                             #6c757d !default;
$gray-700:                             #495057 !default;
$gray-800:                             #343a40 !default;
$gray-900:                             #212529 !default;
$off-black:                            #071013 !default;
$black:                                #000;
 

$grays: () !default;
$grays: map-merge((
 "100": $gray-100,
 "200": $gray-200,
 "300": $gray-300,
 "400": $gray-400,
 "500": $gray-500,
 "600": $gray-600,
 "700": $gray-700,
 "800": $gray-800,
 "900": $gray-900
), $grays);



//* default bootstrap colors
$off-white1:                            rgba(253, 253, 253, 1);
$off-white:                             rgba(250,250,250, 1);
$ghost-white:                           #F8F8FF;
$light-gray:                            #d3d3d3;
$silver:                               #c0c0c0;
$dark-gray:                             #a9a9a9;
$gray:                                 #808080;
$grayrgba:                             rgba(128,128,128,1);
                                      
$blue:                                 #007bff !default;
$indigo:                               #6610f2 !default; 
$purple:                               #6f42c1 !default;
$pink:                                 #e83e8c !default;
$red:                                  #dc3545 !default;
$orange:                               #fd7e14 !default;
$yellow:                               #ffc107 !default;
$green:                                #28a745 !default;
$teal:                                 #20c997 !default;
$cyan:                                 #17a2b8 !default;

$off-white-1:                            rgba(253, 253, 253, 1);
$off-white:                             rgba(250,250,250, 1);
$ghost-white:                           #F8F8FF;
$dirty-white:                           #f9f9f9;        


$bluea-background-color:               rgba(51, 51, 68, .8);
$bluea-background-color2:              rgba(94, 94, 94, .3);
$dark-bg-trans:                        rgba(17, 17, 17, .7);                      
$dkgray-bg-trans:                      rgba(34, 33, 33, 0.3);


$colors:                               () !default;
$colors:                               map-merge((
"blue":                                $blue,
"indigo":                              $indigo,
"purple":                              $purple,
"pink":                                $pink,
"red":                                 $red,
"orange":                              $orange,
"yellow":                              $yellow,
"green":                               $green,
"teal":                                $teal,
"cyan":                                $cyan,
"white":                               $white,
"gray":                                $gray-600,
"gray-dark":                           $gray-800
),                 $colors);



$primary:                              $azureish-white;   
$secondary:                            $babe-blue-eyes;
$accent:                               $spiro-disco-ball;
$accent2:                              $dark-sea-green;
$title-color:					                 $off-black;
$headline-color:				               $off-black;
$deep-gray:                             $gray-800;
$subhead-color:					               inherit;
$display-text-color:			             inherit;
$body-text-color:                      inherit;
$para-color: 					                 inherit;

//*rgba


$link-color:                           $primary;
$link-decoration:                      $orange;
$link-hover-color:                     $accent;
$link-hover-decoration:                none;

$success:                  		         $green;
$info:                     		         $cyan;
$warning:                  		         $yellow;
$danger:                   		         $red;
$light:                    		         $gray-100;
$dark:                     		         $gray-800; 

$theme-colors:                         () !default;
$theme-colors:                         map-merge((
"primary":    				                 $primary,
"secondary":  				                 $secondary,
"accent":     				                 $accent,
"success":    				                 $success,
"info":       				                 $info,
"warning":    				                 $warning,
"danger":     				                 $danger,
"light":      				                 $light,
"dark":       				                 $dark
),                                     $theme-colors);

$body-bg:						                   $white;
$link-color:                           $primary;
$link-decoration:                      none;
$link-hover-color:                     darken($primary, 15%);
$link-hover-decoration:                underline;




//
//Paragraphs
//Style p element.

$carousel-control-color:               $primary;
$carousel-control-opacity:             1;

$paragraph-margin-bottom:              1rem !default;

$navbar-padding:                       1em;


//condensed                            menu nav-links
$nav-link-padding-y:                   2rem;
$nav-link-padding-x:                   2rem;


//expanded                             menu nav-links
$navbar-nav-link-padding-y:			       unset;	
$navbar-nav-link-padding-x:			       2rem;
$navbar-brand-hover-text-decor:		     none;
$navbar-nav-margin:                    0;
$navbar-nav-background:				         rgba($black, .6);

$navbar-background-color: 			       $black;
$toggler-background-color:			       $primary;

$navbar-all-text-hover-color:		       $secondary;
$navbar-brand-hover-color:			       $navbar-all-text-hover-color;
$navbar-dark-hover-color:              $navbar-all-text-hover-color;
$navbar-dark-color:                    $primary;          
$navbar-dark-active-color:             darken($primary, 25%);
$navbar-dark-disabled-color:           red;
$navbar-dark-hamburger-icon:		       rgba($gray-600, .5);
$navbar-dark-toggler-border-color:     rgba($white, .1) !default;

$navbar-light-color:                   rgba($black, .5) !default;
$navbar-light-hover-color:             rgba($black, .7) !default;
$navbar-light-active-color:            rgba($black, .9) !default;
$navbar-light-disabled-color:          rgba($black, .3) !default;
$navbar-light-hamburger-icon:		       unset;
$navbar-light-toggler-border-color:    rgba($black, .1) !default; 


$capabilities-overlay-icon-fill-color: $secondary;
$capabilities-menu-icon-fill-color:    $primary;
$qualifications-icon-fill-color:       $primary;



$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px
) !default;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-columns:                12 !default;
$grid-gutter-width:           30px !default;


$border-radius:               .25rem !default;
$border-radius-lg:            .3rem !default;
$border-radius-sm:            .2rem !default;



// Dropdowns
// Dropdown menu container and contents.

$dropdown-menu-a-color:             $white;
$dropdown-bg:                       $black;
$dropdown-menu-a-letterspacing:     .025em;
$dropdown-border-color:             rgba($black, .15) !default;

$dropdown-divider-bg:               $gray-200 !default;
$dropdown-box-shadow:               0 .5rem 1rem rgba($black, .175) !default;

$dropdown-link-color:               $gray-900 !default;
$dropdown-link-hover-color:         darken($gray-900, 5%) !default;
$dropdown-link-hover-bg:            $gray-100 !default;

$dropdown-link-disabled-color:      $gray-600 !default;

$dropdown-item-padding-y:           .25rem !default;
$dropdown-item-padding-x:           1.5rem !default;
$dropdown-header-color:             $gray-600 !default;


$nav-link-padding-y:                .5rem !default;
$nav-link-padding-x:                1rem !default;
$nav-link-disabled-color:           $gray-600 !default;

$nav-tabs-border-color:             $gray-300 !default;
// $nav-tabs-border-width:             $border-width !default;
$nav-tabs-border-radius:            $border-radius !default;
$nav-tabs-link-hover-border-color:  $gray-200; 
